h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  padding: 0;
  margin: 0;
}
img {
  display: block;
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.section {
  padding-top: 94px;
  padding-bottom: 94px;
}
.container {
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
}
.link {
  text-decoration: none;
  color: inherit;
}
.list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.button {
  font-family: inherit;
  cursor: pointer;
}
.section-title {
  margin-bottom: 50px;

  font-size: 36px;
  line-height: 1.16;
  text-align: center;
}
